<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户信息管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户信息查询</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 用户信息查询 -->
    <el-card class="box-card">
      <el-form
        ref="form"
        label-width="80px"
        :model="form"
        size="mini"
      >
        <el-form-item label="注册时间">
          <el-col :span="8">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="form.date1"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
          <el-col
            class="line"
            :span="1"
          >--</el-col>
          <el-col :span="8">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="form.date2"
              style="width: 100%; margin-left:-25px"
            ></el-date-picker>
          </el-col>
        </el-form-item>
        <small> 用户等级： </small>
        <el-select
          size="mini"
          v-model="value1"
          multiple
          placeholder="请选择"
        >
          <el-option
            v-for="item in options1"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
        <el-button
          type="primary"
          icon="el-icon-search"
          size="mini"
          @click="search"
        >查询</el-button>
      </el-form>

      <!-- 表格 -->
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        stripe
        size='small'
        :lazy='true'
      >
        <el-table-column
          prop="workID"
          label="用户ID"
          width="130"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="activityName"
          label="用户名"
          width="130"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="activityContent"
          label="性别"
          width="120"
          align="center"
        >
        </el-table-column>

        <el-table-column
          prop="publicTime"
          label="用户等级"
          width="140"
          align="center"
        >
        </el-table-column>

        <el-table-column
          prop="publicTime2"
          label="联系方式"
          width="140"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="DateTime"
          label="注册日期"
          align="center"
        >

        </el-table-column>
        <el-table-column
          prop="DateTime1"
          label="登录次数"
          align="center"
        >

        </el-table-column>
        <el-table-column
          prop="address"
          label="操作"
          align="center"
        >
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              circle
              @click="editUser(scope.row)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              circle
              @click="DeleteUser(scope.row.workID)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        :current-page="1"
        :page-sizes="[5, 15, 25, 30]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="200"
      >
      </el-pagination>
    </el-card>
    <!-- 对话框 -->
    <el-dialog
      title="修改用户信息"
      :visible.sync="dialogVisible"
      width="30%"
    >
      <el-form
        ref="getUserInfo"
        :model="getUserInfo"
        label-width="80px"
      >
        <el-form-item label="用户名">
          <el-input v-model="getUserInfo.activityName"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-input v-model="getUserInfo.activityContent"></el-input>
        </el-form-item>
        <el-form-item label="等级">
          <el-input v-model="getUserInfo.publicTime"></el-input>
        </el-form-item>
        <el-form-item label="联系方式">
          <el-input v-model="getUserInfo.publicTime2"></el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button
          type="primary"
          @click="commite()"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<style lang="less" scoped>
small {
  margin-left: 12px;
  color: rgb(90, 86, 86);
}
</style>

<script>
let i = 0;
export default {
  data() {
    return {
      form: {
        date1: ""
      },
      value1: [],
      options1: [
        {
          value: "选项1",
          label: "Vip"
        },
        {
          value: "选项2",
          label: "黄金"
        },
        {
          value: "选项3",
          label: "钻石"
        },
        {
          value: "选项4",
          label: "普通用户"
        },
        {
          value: "选项5",
          label: "会员"
        }
      ],
      tableData: [
        {
          workID: "45611121",
          activityName: "成成",
          activityContent: "男",
          publicTime: "VIP",
          publicTime2: "13845614562",
          DateTime: "2020-05-09",
          DateTime1: "520"
        },
        {
          workID: "45611122",
          activityName: "南南",
          activityContent: "男",
          publicTime: "VIP",
          publicTime2: "13845614562",
          DateTime: "2019-07-16",
          DateTime1: "113"
        },
        {
          workID: "45611123",
          activityName: "绵店",
          activityContent: "男",
          publicTime: "普通用户",
          publicTime2: "13845614562",
          DateTime: "2019-09-10",
          DateTime1: "50"
        },
        {
          workID: "45611124",
          activityName: "燕字",
          activityContent: "女",
          publicTime: "VIP",
          publicTime2: "138456145622",
          DateTime: "2020-05-05",
          DateTime1: "40"
        },
        {
          workID: "45611125",
          activityName: "栗子",
          activityContent: "女",
          publicTime: "VIP",
          publicTime2: "13845614562",
          DateTime: "2020-01-01",
          DateTime1: "44"
        },
        {
          workID: "45611126",
          activityName: "酱香",
          activityContent: "女",
          publicTime: "普通用户",
          publicTime2: "13845614562",
          DateTime: "2019-08-01",
          DateTime1: "120"
        },
        {
          workID: "45611127",
          activityName: "小乔",
          activityContent: "男",
          publicTime: "普通用户",
          publicTime2: "13845614562",
          DateTime: "2020-05-09",
          DateTime1: "51"
        }
      ],
      dialogVisible: false,
      getUserInfo: []
    };
  },
  methods: {
    search() {
      this.$message.success("查询用户信息成功！");
      this.tableData[0].activityName = "娜娜";
      this.tableData[1].activityName = "润润";
      this.tableData[2].activityName = "支梅花";
      this.tableData[3].activityName = "菲菲";
      this.tableData[4].activityName = "芸爱儿";

      this.tableData[5].activityName = "伍子胥";
      this.tableData[6].activityName = "乔丽";

      for (i in this.tableData) {
        this.tableData[i].publicTime = "VIP";
      }
      console.log(this.tableData.length);
    },
    editUser(userInfo) {
      this.dialogVisible = true;
      this.getUserInfo = userInfo;
    },
    commite() {
      this.$message.success("修改用户信息成功！");
      this.dialogVisible = false;
    },
    DeleteUser(id) {
      this.$message.success("删除用户成功！");
      for (i in this.tableData) {
        if (this.tableData[i].workID == id) {
          this.tableData.splice(i, 1);
        }
      }
    }
  }
};
</script>