<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加游戏模式</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 增加游戏模式 -->
    <el-card class="box-card">
      <el-form
        ref="form"
        :model="form"
        label-width="100px"
        size="small"
      >
        <el-form-item label="模式编号">
          <el-input v-model="form.number"></el-input>
        </el-form-item>
        <el-form-item label="模式名称">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="模式内容">
          <el-input
            type="textarea"
            v-model="form.desc"
          ></el-input>
        </el-form-item>
        <el-form-item label="启动日期">
          <el-col :span="11">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="form.date1"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
          <el-col
            class="line"
            :span="2"
          >-</el-col>
          <el-col :span="11">
            <el-time-picker
              placeholder="选择时间"
              v-model="form.date2"
              style="width: 100%;"
            ></el-time-picker>
          </el-col>
        </el-form-item>
       
        <el-form-item label="模式宣传图片：">
          <el-upload
            class="upload-demo"
            action="https://jsonplaceholder.typicode.com/posts/"
            list-type="picture"
          >
            <el-button
              size="small"
              type="primary"
            >点击上传</el-button>
            <div
              slot="tip"
              class="el-upload__tip"
            >只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-button type="success" @click="commit">提交</el-button>
          <el-button class="resetBtn" @click="resetForm('form')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<style lang="less" scoped>
.el-form {
  width: 50%;
}
.InpurDic {
  height: 200px;
}
.resetBtn {
  margin-left: 100px !important;
}
.el-button {
  width: 100px;
}
</style>

<script>
export default {
  data() {
    return {
      form: {
        number: "",
        name: "",
        desc: "",
        date1: ""
      },
      fileList: [
        {
          name: "food.jpeg",
          url:
            "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
        },
        {
          name: "food2.jpeg",
          url:
            "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
        }
      ]
    };
  },
  methods:{
     resetForm(form) {
        this.$refs[form].resetFields();
      },
      commit(){
        this.$message.success('添加游戏模式成功')
         window.location.reload()
      }
  }
};
</script>