import Vue from 'vue'
import VueRouter from 'vue-router'
import Statistics from '../components/statistics1.vue'
import ShopStatistics from '../components/shopStaistics.vue'
import workQuery from '../components/workQuery.vue'
import WorkReview from '../components/workReview.vue'
import ActivityOnline from '../components/activityOnline.vue'
import ActivityWinding from '../components/activityWinding.vue'
import AddActivities from '../components/addActivities.vue'
import StoreInfoQuery from '../components/storeInfoQuery.vue'
import IncreaseStore from '../components/increaseStore.vue'
import IncreaseStoreStatistics from '../components/increaseStoreStatisics.vue'
import UserInfoQuery from '../components/userInfoQuery.vue'
import FBackInfoMenade from '../components/FbackInfoMenage.vue'
import ManageLogQuery from '../components/manageLogQuery.vue'


Vue.use(VueRouter)





export default new VueRouter({
  mode:'hash',
  routes: [
    { path: '/', redirect: '/statistics1' },
    {
      path: '/statistics1',
      component: Statistics,
      redirect: '/ShopStatistics',
      children: [
        {
          path: '/ManagementLogQuery',
          component: ManageLogQuery
        },
        {
          path: '/ShopStatistics',
          component: ShopStatistics
        },
        {
          path: '/feedBackInfoManagement',
          component: FBackInfoMenade
        },
        {
          path: '/UserInfoQuery',
          component: UserInfoQuery
        },
        {
          path: '/workQuery',
          component: workQuery
        },
        {
          path: '/workReview',
          component: WorkReview
        },
        {
          path:'/activityOnline',
          component:ActivityOnline
        },
        {
          path:'/activityWinding',
          component:ActivityWinding
        },
        {
          path:'/addActivity',
          component:AddActivities
        },
        {
          path:'/storeInfoQuery',
          component:StoreInfoQuery
        },
        {
          path:'/increaseStore',
          component:IncreaseStore
        },
        {
          path:'/increaseStoreSTatistics',
          component:IncreaseStoreStatistics
        }
      ]
    },

  ]
})
