<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>信息查询</el-breadcrumb-item>
      <el-breadcrumb-item>急速模式查询</el-breadcrumb-item>
    </el-breadcrumb>
<!-- 急速模式查询 -->
    <el-card class="box-card">
      <el-form
        ref="form"
        label-width="80px"
        :model="form"
        size="mini"
      >

        <el-form-item label="选择类型">
          <el-radio-group v-model="form.resource">
            <el-radio
              class="el-radio__input is-checked"
              label="按月"
            ></el-radio>
            <el-radio label="按季度"></el-radio>
            <el-radio label="按年"></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="选择日期">
          <el-col :span="8">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="form.date1"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
          <el-col
            class="line"
            :span="1"
          >--</el-col>
          <el-col :span="8">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="form.date2"
              style="width: 100%; margin-left:-25px"
            ></el-date-picker>
          </el-col>
        </el-form-item>

        <small>选择店面：</small>
        <el-select
          size="mini"
          v-model="value1"
          placeholder="省份"
        >
          <el-option
            v-for="item in options1"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
        —
        <el-select
          v-model="value2"
          collapse-tags
          style="margin-left: 20px;"
          size="mini"
          class="sel"
          placeholder="地级市"
        >
          <el-option
            v-for="item in options2"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
        —
        <el-select
          v-model="value3"
          collapse-tags
          style="margin-left: 20px;"
          size="mini"
          class="sel"
          placeholder="县级市"
        >
          <el-option
            v-for="item in options3"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>

        <el-button
          type="primary"
          icon="el-icon-search"
          size="mini"
          @click = 'search'
        >查询</el-button>
      </el-form>

      <!-- echarts区域 -->
      <div
        id="main"
        style="width: 900px;height:350px;"
      ></div>

    </el-card>
  </div>
</template>

<style lang="less" scoped>
.el-form-item {
  .el-input {
    width: 260px;
  }
  .Input2 {
    margin-left: 20px;
  }
  .el-button {
    margin-left: 20px;
  }
  .el-radio__input {
    margin-top: -5px;
  }
}
.sel {
  margin-left: -0px !important;
}
small {
  font-size: 14px;
  margin-left: 10px;
  color: rgb(105, 108, 109);
}
#main{
    margin-top: 15px;
}
</style>

<script>
const echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/bar");
require("echarts/lib/chart/line");
require("echarts/lib/component/tooltip");
require("echarts/lib/component/markPoint");
require("echarts/lib/component/title");
require("echarts/lib/component/axisPointer");
export default {
  data() {
    return {
      form: {
        resource: "",
        date1: "",
        date2: "",
        type1: "",
        type2: ""
      },
      value1: [],
      value2: [],
      value3: [],
      options1: [
        {
          value: "选项1",
          label: "四川省"
        },
        {
          value: "选项2",
          label: "贵州省"
        },
        {
          value: "选项3",
          label: "宁夏省"
        },
        {
          value: "选项4",
          label: "安徽省"
        },
        {
          value: "选项5",
          label: "湖南省"
        }
      ],
      options2: [
        {
          value: "选项1",
          label: "成都市"
        },
        {
          value: "选项2",
          label: "贵阳市"
        },
        {
          value: "选项3",
          label: "长沙市"
        },
        {
          value: "选项4",
          label: "天津市"
        },
        {
          value: "选项5",
          label: "武汉市"
        }
      ],
      options3: [
        {
          value: "选项1",
          label: "盘县"
        },
        {
          value: "选项2",
          label: "龙泉驿区"
        },
        {
          value: "选项3",
          label: "蚵仔县"
        },
        {
          value: "选项4",
          label: "龙须六区"
        },
        {
          value: "选项5",
          label: "惠济区"
        }
      ]
    };
  },
  mounted() {
    var myChart = echarts.init(document.getElementById("main"));
    const option = {
      title: {
        text: "普通模式盈利月统计图：",
      },
      color: ["rgb(8, 41, 10)"],
      tooltip: {
        trigger: "axis",
        axisPointer: {
          // 坐标轴指示器，坐标轴触发有效
          type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
        }
      },
      grid: {
        left: "3%",
        right: "4%",
        bottom: "3%",
        containLabel: true
      },
      xAxis: [
        {
          type: "category",
          data: ["一月", "二月", "三月", "四月", "五月", "六月", "七月","八月", "九月","十月","十一月","十二月"],
          axisTick: {
            alignWithLabel: true
          }
        }
      ],
      yAxis: [
        {
          type: "value"
        }
      ],
      series: [
        {
          name: "盈利金额",
          type: "bar",
          barWidth: "30%",
          data: [100000, 120011, 140010, 304014, 452000, 330000, 411020, 452000, 330000,454541, 304014, 452000]
        }
      ]
    };
    myChart.setOption(option);
  },
  methods:{
    search(){
      // console.log(11111111)
      window.location.reload()
      this.$message.success('查询成功')
    }
  }
};
</script>