            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="shanchuByid(scope.row.userId)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        :current-page="1"
        :page-sizes="[5, 15, 25, 30]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="200"
      >
      </el-pagination>
    </el-card>
    <!-- 对话框 -->
    <el-dialog
