<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>碳积分管理</el-breadcrumb-item>
      <el-breadcrumb-item>活动列表管理</el-breadcrumb-item>
    </el-breadcrumb>
<!-- 用户年龄段统计 -->
    <el-card class="box-card">
      <el-form
        ref="form"
        label-width="80px"
        :model="form"
        size="mini"
      >

        <el-form-item label="选择年龄">
          <el-radio-group v-model="form.resource">
            <el-radio
              class="el-radio__input is-checked"
              label="青年"
            ></el-radio>
            <el-radio label="中年"></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="上线时间">
          <el-col :span="6">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="form.date1"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
          <el-col
            class="line"
            :span="0.7"
          > -- </el-col>
          <el-col :span="6">
            <el-date-picker
              placeholder="选择时间"
              v-model="form.date2"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
        </el-form-item>

        <el-form-item label="用户名称">
          <el-input
            v-model="form.type1"
            placeholder="用户ID"
          ></el-input>
        
          <el-button type="primary" @click="queryInfo">查询</el-button>
        </el-form-item>
      </el-form>
      <!-- echarts区域 -->
      <div
        id="main"
        style="width: 750px;height:350px;"
      ></div>
    </el-card>
  </div>
</template>

<style lang="less" scoped>
.el-form-item {
  .el-input {
    width: 260px;
  }
  .Input2 {
    margin-left: 20px;
  }
  .el-button {
    margin-left: 20px;
  }
  .el-radio__input {
    margin-top: -5px;
  }
}
</style>

<script>
const echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/bar");
require("echarts/lib/chart/line");
require("echarts/lib/component/tooltip");
require("echarts/lib/component/markPoint");
require("echarts/lib/component/title");
require("echarts/lib/component/axisPointer");
export default {
  data() {
    return {
      form: {
        resource: "",
        date1: "",
        date2: "",
        type1: "",
        type2: ""
      }
    };
  },
  mounted() {
    var myChart = echarts.init(document.getElementById("main"));
    const option = {
      title: {
        text: "地铁跑酷在2019年5月27日至2020年6月14日用户登录情况",
      },
      tooltip: {
        trigger: "axis",
        axisPointer: {
          type: "shadow"
        }
      },
      legend: {
        data: ["2019.05.27", "2020.06.14"]
      },
      grid: {
        left: "3%",
        right: "4%",
        bottom: "3%",
        containLabel: true
      },
      xAxis: {
        type: "value",
        boundaryGap: [0, 0.01]
      },
      yAxis: {
        type: "category",
        data: ["第10月", "第9月", "第8月", "第7月", "第6月"]
      },
      series: [
        {
          name: "2019年",
          type: "bar",
          data: [6, 2, 4, 5, 10, 7]
        },
        {
          name: "2020年",
          type: "bar",
          data: [1, 6, 6, 8, 12, 6]
        }
      ]
    };
    myChart.setOption(option);
  },
  methods:{
    queryInfo(){
      this.$message.success('查询成功')
      window.location.reload()
    }
  }
};
</script>